<template>
  <div>
    <Header/>
    <div class="form">
      <h2>标题</h2>

      <input type="text" v-model="notes.title">
      <h2>内容</h2>

      <div class="editor">
        <quill-editor
          @change="handleChange"
          v-model="notes.note"
          ref="myQuillEditor"
        >
        </quill-editor>
      </div>

      <div id="label">
        标签
        <el-checkbox-group v-model="selectLabel" class="fll" size="small">
          <el-checkbox-button v-for="label in lables"
                              :label="label" :key="label.index">
            {{label}}
          </el-checkbox-button>
        </el-checkbox-group>
      </div>
      <button @click="handlePut">发布</button>
    </div>

  </div>
</template>

<script>
  import 'quill/dist/quill.snow.css'
  import {quillEditor, Quill} from 'vue-quill-editor'
  import {ImageExtend, QuillWatch} from 'quill-image-extend-module'

  import axios from 'axios'
  import Header from '../components/Header'

  export default {
    name: "write",
    components: {
      Header, quillEditor
    },
    data() {
      return {
        notes: {
          title: "",
          note: "",
          noteText: '',
        },
        content: "",
        selectLabel: [],
        lables: [
          "html", "css", "h5", "js", "es6",
          "ajax", "vue.js", "jquery.js",
          "react.js", "require.js", "ui", "anlmate"
        ]
      }
    },
    methods: {
      handlePut() {
        let title = this.notes.title;
        let note = this.notes.note;
        let arr = "123455"
        let noteText = this.notes.noteText.substring(0, 200)
        let classify = this.selectLabel;

        if (title && note) {
          axios.post('/note/addNote', {title, note, noteText, classify}).then(res => {
            // console.log(res.data);
            if (res.data.code == 200) {
              alert("发布成功");
              this.notes.title = "";
              this.notes.note = "";
            } else if (res.data.code == 400) {
              alert("发布失败")
            }
          })
        } else if (title == "") {
          alert("输入标题不能为空")
        } else if (note == "") {
          alert("输入内容不能为空")
        }
      },
      handleChange({quill, html, text}) {
        // console.log(this.notes.noteText);
        this.notes.noteText = text;
      }
    }
  }
</script>

<style scoped lang="less">
  .form {
    width: 800px;
    margin: 80px auto;
    textarea {
      font-size: 18px;
      padding: 30px 30px;
      width: 700px;
      height: 500px;
      resize: none;
      outline: none;
    }
    button {
      width: 120px;
      height: 30px;
      border: none;
      background: red;
      color: #fff;
      outline: none;
      font-size: 18px;
    }
    h2 {
      margin: 20px 0;
    }
    input {
      padding: 0 30px;
      font-size: 22px;
      height: 50px;
      width: 730px;
      outline: none;
    }
    #label {
      margin: 20px 0;
    }
  }
</style>



